import { _decorator, Component, Node, Vec3 } from 'cc';
import { JoystickController } from './JoystickController';
const { ccclass, property } = _decorator;

@ccclass('Player')
export class Player extends Component {
    @property(Node) player: Node = null;
    @property(Number) movementSpeed: number = 200; // Pixel/s olarak hareket hızı

    @property(JoystickController) joystickController: JoystickController = null;

    update(deltaTime: number) {
        if (this.joystickController) {
            const direction = this.joystickController.getDirection();
            if (direction.length() > 0) {
                // Hareket vektörünü hesapla
                const moveVector = new Vec3(direction.x, direction.y, 0).multiplyScalar(this.movementSpeed * deltaTime);
                // Yeni pozisyonu ayarla
                const newPosition = this.player.getPosition().add(moveVector);
                this.player.setPosition(newPosition);
            }
        }
    }
}


